import './card-page.scss';
import BasePage from '../BasePage/base-page';
import BusArrivalContainer from '../../containers/BusArrivalContainer/bus-arrival-container';
import React, { useState } from 'react';

const CardPage = () => {
  const [isLetterOut, setIsLetterOut] = useState(false);
  // $(document).ready(function () {
  //     $('.container').mouseenter(function () {
  //         $('.card').stop().animate({
  //             top: '-90px'
  //         }, 'slow');
  //     }).mouseleave(function () {
  //         $('.card').stop().animate({
  //             top: 0
  //         }, 'slow');
  //     });
  // });
  return (
    <div className="page">
      <div className="title">
        <h1>Chi sent you a card!</h1>
      </div>
      <div
        className="container"
        role="button"
        onClick={() => setIsLetterOut(!isLetterOut)}
        onKeyDown={() => setIsLetterOut(!isLetterOut)}
        tabIndex={0}
      >
        <div className="valentines">
          <div className="envelope" />
          <div className="front" />
          <div className="heart" />
          <div
            className={`card${isLetterOut ? ' isLetterOut' : ' isNotLetterOut'}`}
            // style={{
            //   transition: 'transform 500ms linear',
            //   top: isLetterOut ? '-90px' : '0',
            // }}
          >
            <div className="text">
              <br />Chi<br />{'♡'}<br />BUBU
            </div>
          </div>
          <div className="hearts">
            <div className="one" />
            <div className="two" />
            <div className="three" />
            <div className="four" />
            <div className="five" />
          </div>
        </div>
      </div>
      <div className="shadow" />
      <div style={{ height: '150vh' }} />
      <div style={{ textAlign: 'center' }}>
        <p>
          {'Dear my Bubu,'}
        </p>
        <p>
          {'This would be my first card for you in a long while.\
           I thought for a long while about what would you find interesting, and I have prepared a poem to express my gratitude for having you in my life.'} <br /><br />
          {'Hope bu would like it.'}
        </p>
        <br />
        <br />
        ------------✱------------
        <br />
        <br />
        <br />
        <p>
          {'In the realm of mortals, where words take flight,'}<br />
          {'I deliver this poem with all my might,'}<br />
          {'To profess all the love I hold,'}<br />
          {'For you, my dearest, more precious than gold.'}<br />
        </p>
        <p>
          {'Your eyes, like stars in the midnight sky,'}<br />
          {'Sparkling in such a way that time slips by,'}<br />
          {'They speak of a place only we could share,'}<br />
          {'A land which holds mysteries, beyond compare.'}<br />
        </p>
        <p>
          {'Far and yonder, bards sang of a magical realm,'}<br />
          {'Of a land, where magic dazzles and overwhelm,'}<br />
          {'Where penguins waddle and chicks take flight,'}<br />
          {'And where our hearts can be oh so light.'}<br />
        </p>
        <p>
          {'Hand in hand, we sail from shore to shore,'}<br />
          {'Exploring a world where our love shall soar,'}<br />
          {'With each passing moment, our bond grows strong,'}<br />
          {'In this land of enchantment, where we belong.'}<br />
        </p>
        <p>
          {'In your embrace, I find solace and peace,'}<br />
          {'A haven of love where all worries cease,'}<br />
          {'Your arms, a cradle, snug and secure,'}<br />
          {'Where I feel your love, so gentle and pure.'}<br />
        </p>
        <p>
          {'Together we\'ve woven a tapestry so rare,'}<br />
          {'Of laughter, adventures, and moments we share,'}<br />
          {'With you by my side, I fear no strife,'}<br />
          {'For you are the conflagration, scorching all I face in life.'}<br />
        </p>
        <p>
          {'So here, in this poem, I profess anew,'}<br />
          {'My love for you, forever true,'}<br />
          {'No words can capture the depth and grace,'}<br />
          {'Of the love we share, in this sacred space.'}<br />
        </p>
        <p>
          {'Forever and always, together we\'ll be,'}<br />
          {'Bound by a love that\'s wild and free,'}<br />
          {'In this poem, my heart\'s open wide,'}<br />
          {'For you, my love, are my eternal guide.'}<br />
        </p>
        <br />
        <br />
        ------------✱------------
        <br />
        <br />
        <br />
        <p>
          {'I am sorry that I could not prepare something more fitting of my love for you, however do know that my heart would always be with you.'}<br />
          {'May our journey be filled with endless blessings.'}<br />
        </p>
        <br />
        <p>
          {'With all my love,'}<br />
          {'Chi'}<br />
        </p>
      </div>
      <div style={{ textAlign: 'center', paddingTop: '110vh', paddingBottom: '30px' }}>
        <p>
          {'from chichi, with ♥'}
        </p>
      </div>
    </div>
    // <BasePage isLoading={!isLoaded}>
    //   <h1>test</h1>
    //   <BusArrivalContainer onSetIsLoaded={value => setIsLoaded(value)} />
    // </BasePage>
  );
};

export default CardPage;
