import './treasure-1-page.scss';
import BasePage from '../BasePage/base-page';
import BusArrivalContainer from '../../containers/BusArrivalContainer/bus-arrival-container';
import React, { useState } from 'react';
import { Button, TextField } from '@mui/material';
// eslint-disable-next-line
const emperorJailImg = require('../../../img/emperor_jail.PNG');
// eslint-disable-next-line
const emperorNotfoundImg = require('../../../img/emperor_notfound.PNG');

const CardPage = () => {
  const [isFree, setIsFree] = useState(false);
  const [value, setValue] = useState('');
  const lockPin = '071117';
  // $(document).ready(function () {
  //     $('.container').mouseenter(function () {
  //         $('.card').stop().animate({
  //             top: '-90px'
  //         }, 'slow');
  //     }).mouseleave(function () {
  //         $('.card').stop().animate({
  //             top: 0
  //         }, 'slow');
  //     });
  // });
  return (
    <div className="page">
      <br />
      <br />
      {!isFree && (
        <>
        <img src={emperorJailImg} style={{ objectFit: 'cover', width: '80vw' }} alt="test" />
        <h1 style={{ textAlign: 'center' }}>The Caesar Penguin is in jail!</h1>
        <br />
          <br />
        <TextField
          id="outlined-basic"
          label="PIN"
          variant="outlined"
          value={value}
          onChange={e => {
            const regex = /^[0-9\b]+$/;
            if (e.target.value === '' || regex.test(e.target.value)) {
              setValue(e.target.value);
              setIsFree(e.target.value === lockPin);
            }
          }}
        />
          <br />
        <div style={{ textAlign: 'center' }}>
          ------------✱------------
          <br />
          <br />
          <p>
            {'Free me! I wanna waffles...'}
          </p>
          <br />
          ------------✱------------
        </div>
        </>
      )}
      {isFree && (
        <>
          <img src={emperorNotfoundImg} style={{ objectFit: 'cover', width: '80vw' }} alt="test" />
          <h1 style={{ textAlign: 'center' }}>The Caesar Penguin is freed! He cannot find cheegly, but he left you a hint!</h1>
          <br />
          <div style={{ textAlign: 'center' }}>
            ------------✱------------
            <br />
            <br />
            <p>
              {'P jhuuva mpuk tf dhmmslz, iba pa pz vrhf >< P zhd jollnsf iyvbnoa av aol shynlza ilkyvvt.'}
            </p>
            <br />
            <br />
            ------------✱------------
          </div>
        </>
      )}
      <div style={{ textAlign: 'center', paddingTop: '50vh', paddingBottom: '30px' }} />
    </div>
    // <BasePage isLoading={!isLoaded}>
    //   <h1>test</h1>
    //   <BusArrivalContainer onSetIsLoaded={value => setIsLoaded(value)} />
    // </BasePage>
  );
};

export default CardPage;
