import './bus-arrival-container.scss';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';
import BuchiBreadcrumbs from '../../../component/BuchiBreadcrumbs/buchi-breadcrumbs';
import BuchiChecklist from '../../../component/BuchiChecklist/buchi-checklist';
import BuchiImage from '../../../component/BuchiImage/buchi-image';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import commerce from '../../../lib/commerce';
import lodash from 'lodash';
import moment from 'moment';
import {
  Button,
  Pagination,
  PaginationItem,
  Stack,
} from '@mui/material';
import { Grid } from '@material-ui/core';
import { useLocation, useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { useWindowDimension } from '../../../hooks/bubuchichis-hook';

const testFn = () => {
  return axios.get('http://localhost:3001/bus/83139', {
    headers: {
      Accept: 'application/json',
    },
  })
  .then(response => {
    // eslint-disable-next-line
    console.log(response);
  })
  .catch(error => {
    // eslint-disable-next-line
    console.log(error);
  });
};

const BusArrivalContainer = ({ onSetIsLoaded }) => {
  const [lastUpdated, setLastUpdated] = useState('');
  const [LTAData, setLTAData] = useState({});
  const t = useTranslation()[0];
  const breadcrumbs = [
    {
      name: 'homePage',
      label: t('navBar.homePage'),
      path: '/',
    },
    {
      name: 'busArrivalPage',
      label: t('navBar.busArrivalPage'),
      path: '/bus',
    },
  ];

  useEffect(() => {
    refreshData();
  }, []);

  const refreshData = () => {
    axios.get('https://bubuchichis-webpage-backend.onrender.com/bus/80199', {
      headers: {
        'Access-Control-Allow-Origin': '*',
        Accept: 'application/json',
      },
    })
    .then(response => {
      // eslint-disable-next-line
      console.log(response);
      setLTAData(response.data);
      onSetIsLoaded(true);
      setLastUpdated(moment().format('YYYY-MM-DD HH:mm:ss'));
    })
    .catch(error => {
      // eslint-disable-next-line
      console.log(error);
    });
  };

  // rounded up to nearest integer
  const getTimeFromNowStr = dateStr => {
    const busTime = moment(dateStr, 'YYYY-MM-DDTHH:mm:ss Z');
    const currTime = moment();
    const timeToBus = moment.duration(busTime.diff(currTime));
    let secsToBus = Math.ceil(timeToBus.asSeconds());
    let busPast = false;
    if (secsToBus < 0) {
      busPast = true;
      secsToBus *= -1;
    }
    const minsToBus = Math.floor(secsToBus / 60);
    const remSecsToBus = secsToBus % 60;
    return `${minsToBus > 0 ? `${minsToBus} min${minsToBus > 1 ? 's ' : ' '}` : ''}${remSecsToBus} sec${remSecsToBus > 1 ? 's' : ''}${busPast ? ' ago' : ''}`;
  };

  // display for a single service
  const getDisplayForBusService = serviceObj => {
    return (
      <>
        <h4>Bus service {serviceObj.ServiceNo}:</h4>
        {'NextBus' in serviceObj && 'EstimatedArrival' in serviceObj.NextBus ? null : (
          <p>Service has ended for the day</p>
        )}
        {getDisplayRowForNextBus(serviceObj, 1, 'NextBus')}
        {getDisplayRowForNextBus(serviceObj, 2, 'NextBus2')}
        {getDisplayRowForNextBus(serviceObj, 3, 'NextBus3')}
      </>
    );
  };

  const getDisplayRowForNextBus = (serviceObj, busIdx, busKey) => {
    return (
      busKey in serviceObj && 'EstimatedArrival' in serviceObj[busKey] ? (
        <p>
          <strong>Bus {busIdx} - </strong>
          {getTimeFromNowStr(serviceObj[busKey].EstimatedArrival)} - {serviceObj[busKey].VisitNumber <= 1 ? 'towards Tanjong Rhu' : 'towards Kallang MRT'}
        </p>
      ) : null
    );
  };

  return (
    <Grid container className="page-width">
      <Grid item xs={12}>
        <BuchiBreadcrumbs breadcrumbs={breadcrumbs} />
      </Grid>
      <Grid item xs={12}>
        <h1 className="page-title">Bus Arrival</h1>
        <p><strong>Last updated: </strong>{lastUpdated}</p>
      </Grid>
      <Grid item xs={12}>
        <Button onClick={() => refreshData()} className="site-header__icon site-header__darkmode">
          CLICK TO REFRESH
        </Button>
        <h2>Stadium MRT (80199):</h2>
        {'Services' in LTAData && LTAData.Services.length > 0 ?
          getDisplayForBusService(LTAData.Services[0]) : (
          <p>Services at this bus stop has ended for the day</p>
        )}
        <br /><br /><br /><br /><br />
        <h3>JSON for debug</h3>
        <span style={{ whiteSpace: 'pre-wrap' }}>{JSON.stringify(LTAData, null, 2)}</span>
      </Grid>
    </Grid>
  );
};

BusArrivalContainer.propTypes = {
  onSetIsLoaded: PropTypes.func.isRequired,
};

export default BusArrivalContainer;
